 18px;\n      @media (min-width: $globalMin) {\n        font-size: 18px;\n      }\n      font-size: calc(18px + (100vw - 480px) / 260);\n      @media (min-width: $globalMax) {\n        font-size: 20px;\n      }\n      //   @include piecewise(font-size, 18, 20);\n    }\n  }\n}\n","@import './colors.scss';\n\n@mixin boxShadow() {\n  box-shadow: 0 1px 2px $grey;\n  transition: box-shadow 75ms ease;\n  &:hover {\n    box-shadow: 0 2px 3px $grey;\n  }\n}\n\n@mixin clickAnimation($opacity: 0.5, $time: 150ms) {\n  opacity: 1;\n  transform: $time ease;\n  &:hover {\n    transform: translateY(-1px);\n    opacity: $opacity;\n  }\n  &:active,\n  &.active {\n    transform: translateY(2px);\n  }\n}\n\n@mixin antialiasing() {\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n@mixin grabbing() {\n  cursor: grabbing;\n  cursor: -moz-grabbing;\n  cursor: -webkit-grabbing;\n}\n@mixin noSelect() {\n  -webkit-touch-callout: none;\n  -webkit-user-select: none;\n  -khtml-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n}\n"],"sourceRoot":""}]);
